.center-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100%;
  height: 100vh;
  max-height: 100%;
  background-color: transparent;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: #e4e5eb;
  border-radius: 20px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
